
/*+========================SLIDER============================*/
.slick-slider {
    position: relative;
    display: block;
    box-sizing: border-box;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    -webkit-touch-callout: none;
    -khtml-user-select: none;
    -ms-touch-action: pan-y;
    touch-action: pan-y;
    -webkit-tap-highlight-color: transparent;
}

.slick-list {
    position: relative;
    display: block;
    overflow: hidden;
    margin: 0;
    padding: 0 !important;
}

.slick-list:focus {
    outline: none;
}

.slick-list.dragging {
    cursor: pointer;
    cursor: hand;
}

.slick-slider .slick-track,
.slick-slider .slick-list {
    -webkit-transform: translate3d(0, 0, 0);
    -moz-transform: translate3d(0, 0, 0);
    -ms-transform: translate3d(0, 0, 0);
    -o-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
}

.slick-track {
    position: relative;
    top: 0;
    #{theme('direction.left','value')}: 0;
    display: block;
    margin-left: auto;
    margin-right: auto;
}

.slick-track:before,
.slick-track:after {
    display: table;
    content: '';
}

.slick-track:after {
    clear: both;
}

.slick-loading .slick-track {
    visibility: hidden;
}

.slick-slide {
    display: none;
    float: #{theme('direction.left','value')};
    height: 100%;
    min-height: 1px;
}



.slick-slide img {
    display: block;
    margin: auto;
}

.slick-slide.slick-loading img {
    display: none;
}

.slick-slide.dragging img {
    pointer-events: none;
}

.slick-initialized .slick-slide {
    display: block;
}

.slick-loading .slick-slide {
    visibility: hidden;
}

.slick-vertical .slick-slide {
    display: block;
    height: auto;
    border: 1px solid transparent;
}

.slick-arrow.slick-hidden {
    display: none;
}


.slick-arrow {
    position: absolute;
    top: 50%;
    background: #fff;
    width: 56px;
    height: 56px;
    border-radius: 100%;
    font-size: 0;
    box-shadow: 0 0 10px 5px rgba(0, 0, 0, 0.1);
    margin: -28px 0 0 0;
    z-index: 1;
    border: none;
    cursor: pointer;
}

.slick-arrow:before {
    width: 14px;
    height: 14px;
    position: absolute;
    #{theme('direction.left','value')}: 50%;
    top: 50%;
    margin: -7px 0 0 0; margin-#{theme('direction.left','value')}: -7px;
    content: "";
    border-#{theme('direction.left','value')}: 2px solid #333;
    border-bottom: 2px solid #333;
}

.slick-arrow.slick-prev {
  left: 0;
}

.slick-arrow.slick-prev:before {
    transform: rotate(135deg);
    -webkit-transform: rotate(135deg);
    -ms-transform: rotate(135deg);
    margin-#{theme('direction.left','value')}: -6px;
}

.slick-arrow.slick-next {
    right: 0;
}

.slick-arrow.slick-next:before {
    transform: rotate(-45deg);
    -webkit-transform: rotate(-45deg);
    -ms-transform: rotate(-45deg);
    margin-#{theme('direction.left','value')}: -10px;
}

.slick-arrow.slick-disabled {
    opacity: 0;
}

.slick-dots {
 
    text-align: center;
    li{
        display: inline-block;
        vertical-align: top;
         margin-#{theme('direction.right','value')}: 10px;
        button{
            width: 30px;
            height: 6px;
            border: none; font-size: 0; background: rgba(0, 0, 0, 0.2);
            
        }
        &.slick-active{
           button{ background:theme('color.primary');}
        }
        
    }
   
}






